import React from 'react';
import { Table,Button } from 'semantic-ui-react';

export default function TableView({ rows, handleAdd, handleEdit,handleShowAll }) {
  return (
    <>
    <Button onClick={handleShowAll}></Button>
      <Table celled unstackable>
        <Table.Header>
          <Table.Row>
          <Table.HeaderCell width={2}></Table.HeaderCell>
            <Table.HeaderCell width={2}></Table.HeaderCell>
            <Table.HeaderCell width={2}></Table.HeaderCell>
            <Table.HeaderCell width={2}></Table.HeaderCell>
            <Table.HeaderCell width={2}></Table.HeaderCell>
            <Table.HeaderCell width={2}>
              <Button primary onClick={handleAdd}>
                
              </Button>
            </Table.HeaderCell>
          </Table.Row>
        </Table.Header>

        <Table.Body>
          {rows.map((row, index) => {
            return (
              <Table.Row key={row.id}>
                <Table.Cell>{row.date}</Table.Cell>
                <Table.Cell>{row.name}</Table.Cell>
                <Table.Cell>{row.qty}</Table.Cell>
                <Table.Cell>{row.cost}</Table.Cell>
                <Table.Cell>{Math.round(row.qty*row.cost)}</Table.Cell>
                <Table.Cell>
                  <Button onClick={() => handleEdit(row, index)}></Button>
                </Table.Cell>
              </Table.Row>
            );
          })}
        </Table.Body>
      </Table>
    </>
  );
}
