import React, { useEffect, useState } from 'react';
import { db } from '../../../utils/firebase';

import TableView from './components/TableView';
import EditForm from './components/EditForm';

export default function Index({
  transactionRows,
  setTransactionRows,
  statRows,
  handleShowAll
}) {
  /**********  ************/
  // 
  const defaultRow = {
    date: new Date().toISOString().substring(0, 10), //
    name: '', // 
    cost: '', // 
    qty: '', //
  };

  // 
  const [row, setRow] = useState(defaultRow);
  // 
  const [rowIndex, setRowIndex] = useState(defaultRow);
  // 
  const [open, setOpen] = useState(false);

  // firebase 
  const colName = 'stockTransaction';

  /**********  ************/
  const handleAdd = () => {
    setOpen(true);
    setRowIndex(-1);
    setRow(defaultRow);
  };

  // ()
  const handleSave = () => {
    // 
    if (rowIndex == -1) {
      db.collection(colName)
        .add(row)
        .then((docRef) => {
          // 
          setTransactionRows([...transactionRows, { ...row, id: docRef.id }]);
        });
    } else {
      // 
      db.collection(colName)
        .doc(row.id)
        .update(row)
        .then(() => {
          // 
          const tempRows = transactionRows.slice();
          Object.assign(tempRows[rowIndex], row);
          setTransactionRows(tempRows);
          setRowIndex(-1);
        });
    }

    // 
    setOpen(false);
    // 
    setRow(defaultRow);
  };

  // 
  const handleDelete = () => {
    db.collection(colName)
      .doc(row.id)
      .delete()
      .then(() => {
        setTransactionRows(transactionRows.filter((obj) => obj.id != row.id));
        setOpen(false);
      });
  };

  // 
  const handleEdit = (editedRow, index) => {
    setRow(editedRow);
    setRowIndex(index);
    setOpen(true);
  };

  // 
  const handleChange = (e) => {
    setRow({ ...row, [e.target.name]: e.target.value });
  };

  // 
  const handleStockNameChange = (e, { value }) => {
    setRow({ ...row, name: value });
  };

  return (
    <div>
      <TableView
        handleShowAll={handleShowAll}
        rows={transactionRows}
        handleEdit={handleEdit}
        handleAdd={handleAdd}
      />
      <EditForm
        statRows={statRows}
        open={open}
        setOpen={setOpen}
        row={row}
        handleSave={handleSave}
        handleDelete={handleDelete}
        handleChange={handleChange}
        handleStockNameChange={handleStockNameChange}
      />
    </div>
  );
}
